body {
	color: var(--color-main);
	margin: 0;
	padding: 0;

	position: relative;

	overflow-x: hidden;
}

#webview{
	display: flex;
}

a:hover{
	text-decoration: underline;
}

a:active{
	text-decoration: underline;
}

//Table of Contents
.toc-wrap{
	width: 20%;
	height: 100%;
	background-color: #b19cd9;
	position: fixed;
	top: 0;
	left: 0;

	z-index: -1;
	overflow: scroll;
}

li.toclevel-1,
li.toclevel-2 {
	list-style: none;

	border-left: solid 0.5px;
    padding-left: 5px;
}

li.toclevel-3,
li.toclevel-4{
	display: none;
}

a[href*="Test_Area"],
a[href*="Demo_Scriptotheek"]{
	display: none;
}

//Picking links on the field 
//chapter Setting_conditions
a[href*="Platframe_Postscript"],
a[href*="#Re-,_Un-,_Defining_Tools"],
a[href*="Open-source_Parenting"],
a[href*="A_history_of_consensus_decision_making"],
a[href*="Algorithmic_Consensus"],
a[href*="Mycelines:_A_Sympoetic_Imagination"],

//chapter How-to's
a[href*="Mud_Batteries"],
a[href*="Gerwani_and_Api_Kartini._Indonesian_Womens_Movement"],
a[href*="Display(ing)"],

//chapter Prompts
a[href*="Prompts_for_hanging_out_in_a_spreadsheet"],
a[href*="Propositioning"],
a[href*="Roleplaying_in_Etherpad"],

//chapter Scripted Syllabi
a[href*="Interfacial-Workouts"],
a[href*="wiki_reflections"],

//chapter Active Bibliographies
a[href*="Workshop_Histories_and_Practices"],

//DemoScript
a[href*="Demo_Scriptotheek"]{
	display: none;
}

.content{
	position: absolute;

	width: 75%;
	left: 20%;
}

.chapter{
	position: relative;
	// width: 75%;
	// left: 20%;
	background-color: white;
	top: 0;

	height: auto;

	box-shadow: 10px 10px 40px 0px rgba(0,0,0,0.94);
	-webkit-box-shadow: 10px 10px 40px 0px rgba(0,0,0,0.94);
	-moz-box-shadow: 10px 10px 40px 0px rgba(0,0,0,0.94);
}

// all chapters
.article{
	position: relative;
	display: none;
	padding: 1%;
}

// .chapter:target,
.article:target{
	display: block;
}

.btn-wrapper{
	position: fixed;
	width: 75%;
	left: 20%;
	top: 0;
	background-color: transparent;

    // box-shadow: 0 15px 15px -5px white;
	z-index: 8934239;
	text-align: right;
}

.change-behind{
	font-size: 50px;
	padding: 10px;
	cursor: pointer;
	background-color: transparent;
	border: none;
}

// 

img {
	width: auto; // height is set with javascript
	height: auto;
	max-width: 50%;
	max-height: 80%;
	display: block;
	margin: 12pt auto;
}

.wikitable .filehistory{
	display: block;
}

.test-area{
	visibility: hidden;
}

.pagedjs_pages .two-columns {
    columns: 2;
}

//scriptothek
.Demo-script{
	position: fixed;
	width: 21.5%;
	height: 100vh;

	margin: 0;
	padding: 0;

	right: 0!important;
	top: 0;
	background-color: yellow!important;

	z-index: -1;
	overflow: scroll!important;

	visibility: visible;

	box-shadow: 10px 10px 40px 0px rgba(0,0,0,0.94);
	-webkit-box-shadow: 10px 10px 40px 0px rgba(0,0,0,0.94);
	-moz-box-shadow: 10px 10px 40px 0px rgba(0,0,0,0.94);
}

.script-image {
	// width: 10%; // height is set with javascript
	position: relative;
	right: 0;
	cursor: pointer;
}

.script-image .thumbimage{
	border: solid black;
	width: 10%;
}

// .thumb .tright{
// 	margin-bottom: 5%;
// }

.hide-from-book{
	display: none;
	position: relative;
}

.slideshow-buttons{
	width: 96.5%;
	position: fixed;
	// background-color: #b19cd9;
	right: 0;
	z-index: 5;
	top:50%;
	display: none;
}

.close-wrapper{
	width: 96.5%;
	position: fixed;
	// background-color: #b19cd9;
	right: 0;
	z-index: 5;
	top:0;
	display: none;
}

.prev,
.next,
.close{
	border:none;
	display:inline-block;
	padding:8px 16px;
	vertical-align:middle;
	overflow:hidden;
	text-decoration:none;
	color:inherit;
	background-color:inherit;
	text-align:center;
	cursor:pointer;
	white-space:nowrap;
	font-size: 50px;
}

.prev{
	position:absolute;
	left:0%;
	// transform:translate(0%,-50%);
	// -ms-transform:translate(-0%,-50%)
}

.next{
	position:absolute;
	right:0%;
	// transform:translate(0%,-50%);
	// -ms-transform:translate(-0%,-50%)
}


